<template>
    <div class="navbar">
            <nav class="transparent">
                <div class="nav-wrapper">
                   
                    <!-- The "Hello username" feature was causing issues on smaller screens
                         <a href="" class="brand-logo hide-on-small-only left">Gunnar Anderson</a>
                    <a href="#" class="brand-logo hide-on-med-and-up left">Gunnar</a>
                    -->
                    <submenus></submenus>
                </div>
            </nav>
        </div>
</template>

<script>
    import submenus from "./SubMenus"
    export default {
        name: 'Nav',
        mounted(){
                $(".dropdown-button").dropdown();
        },
        components:{submenus},
        activated(){
            console.log("activated")
            this.$nextTick(()=>{
                console.log("Next tick")
                setTimeout(()=>{
                    $(".dropdown-button").dropdown();
                }, 1000)
                
            })
        }
    }

</script>

<style scoped>
    .brand-logo {
        margin-left: 10px;
    }
    .menu-padding {
        margin-right: 30px;
    }
    .nav-wrapper {
        background-color: transparent;
    }
    .navbar{
        background-color: transparent;
    }
</style>


