import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Gmod from '@/components/Gmod'
import Hammer from '@/components/Hammer'
import ZARP from '@/components/ZARP'
Vue.use(Router)
const about = "#Interests"
const anchors = {
  Gmod: about,
  Hammer: about,
  "ZARP": about
}


export default new Router({
  scrollBehavior(to, from, savedPosition) {
    if (anchors[to.name]) {
      return {
        selector: anchors[to.name]
      }
    }
    console.log(to.hash)

    if (to.hash) {
      return {
        selector: to.hash
      }
    }
  },
  mode: 'history',
  base: __dirname,
  routes: [{
    path: '/',
    name: 'Hello',
    component: Hello,
    children: [{
      path: '#gmod',
      name: 'Gmod',
      component: Gmod
    }, {
      path: '#hammer',
      name: 'Hammer',
      component: Hammer
    }, {
      path: '#fp-zarp',
      name: 'ZARP',
      component: ZARP
    }]
  }]
})



