<template>
    <div>
    <ul id="dropdown1" class="dropdown-content section">
            <li><router-link href="#Interests" :to="{ name: 'Gmod' }" class="purple-text">Garrys Mod</router-link></li>
            <li><router-link href="#Interests" :to="{ name: 'ZARP' }"class="purple-text">FP/ZARP</router-link></li>
            <li><router-link href="#Interests" :to="{ name: 'Hammer' }"class="purple-text">Mapping</router-link></li>
    </ul>
   <ul id="nav-mobile section " class="right menu-padding">
        <li><router-link to="/#About" href="#About">About</router-link></li>
        <li><a class="dropdown-button" href="#" data-activates="dropdown1">Interests<i class="material-icons right">arrow_drop_down</i></a></li>
        <li><router-link to="/#Showcase" href="#Showcase">Showcase</router-link></li>
    </ul>
    </div>
</template>

<<script>
    export default {
        name: 'submenus',
        mounted(){
                $(".dropdown-button").dropdown();
                //$('.scrollspy').scrollSpy();
                this.$nextTick(()=>{
                console.log("Next tick")
                setTimeout(()=>{
                    $(".dropdown-button").dropdown();
                    //$('.scrollspy').scrollSpy();
                }, 1000)
                
            })
        },
        activated(){
            console.log("activated")
            this.$nextTick(()=>{
                console.log("Next tick")
                setTimeout(()=>{
                    $(".dropdown-button").dropdown();
                   // $('.scrollspy').scrollSpy();
                }, 1000)
                
            })
        }
    }
</script>

<style scoped>
</style>


