
  <template>
    <div class="section">
      <div class="row">
        <div class="col s12 center">
          <h3><i class="mdi-content-send purple-text">Garry's Mod</i></h3>
          <p class="left-align light flow-text">A sandbox physics based game, Garry's Mod created by Facepunch. Utilizes a Lua based scripting language for a C++
            interface to allow developers almost limitless moding possibilities. Gmod as it is more commonly know was first
            Introduction to the concepts of programming and the biggest reason I started to actively program. Over the past
            4 years I have created and maintained many addons/gamemodes/maps/servers for the use in Gmod. Utilizing a trusted
            3rd party website (gmodstore.com) I have been able to successfully sell multiple scripts to hundreds of different
            Customers.</p>
          <div class="carousel carousel-slider center" :style="{height: cheight}" data-indicators="true">
            <div class="carousel-item grey white-text" href="#one!">
              <h2>Man of the Match</h2>
              <p class="white-text flow-text">Man of the match is a great way to reward your players for doing well in various game modes. At the end of
                each map Man of the Match (MOTM) will automatically calculate the top 3 players. Rewarding them with a selection
                Of easy to add/configure prizes. Anything from speed/health boosts to points and karma boosts.</p>
                <img src="https://s3.scriptfodder.com/script_banners/764cf9bb12b5daaf34d1764eb317e290.png" alt="">
            </div>
            <div class="carousel-item grey white-text" href="#two!">
              <h2>RapSheet</h2>
              <p class="white-text flow-text">Rapsheet is a brand new way for you to keep track of the punishments that a player has accumulated, punishments are stored forever and are available for admins to view in a sleek looking menu. Admins abusing commands? Now you can find out who is doing what, without having to search through hundreds of text files.</p>
               <img src="https://s3.scriptfodder.com/script_banners/97af4d502cba57242575f78936d97120.png" alt="">
            </div>
            <div class="carousel-item grey white-text" href="#three!">
              <h2>Clean Chat</h2>
              <p class="white-text flow-text">Clean chat is a simple way for server owners to stop players from using and spamming offensive language in chat. Put a stop once and for all to trolls and racists who come on your server just to chat spam.
              <br>
              <br>
              <br>
              </p>
              <img src="https://s3.scriptfodder.com/script_banners/e68056704946208795747f85bcd878ce.png" alt="">
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>

<<script>
    export default {
        name: 'gmod',
         mounted: function () {
             $('.carousel.carousel-slider').carousel({fullWidth: true});
             setTimeout(()=>{
                 $('.carousel.carousel-slider').css("height", "400px");
             },1000)
            
        },
        data(){
            return {cheight: 400}
        }
    }
</script>

<style scoped>
    .padding {
        margin-bottom: 20px;
    }
    .carousel {
        height: 500px;
    }
</style>


